
*{
    font-family: sans-serif;
   
}

body{
    padding: 60px;
}


h2{
   position: relative;
    font-size: 40px;
    margin: 0 auto;
    color: green;
    left: 45%;
    padding: 40px 0;
}

input{
width: 100%;
  border: 4px solid orange;
    font-size: 1.2em;
    margin: 20px 0;
}

::placeholder{
    text-align: center;
}

hr{
    height: 20px;
    background-color: black;
}


.card{
    width: 300px;
    height: 300px;
    align-content: center;
    border: 1px solid orange;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card p{
    text-align: center;
    padding: 10px;
}

img{
    width: 100%;
    height: 250px;
    max-width: 100%;
   max-height: 100%;
 
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}